export default {
	data() {
		return {
			userInfo: JSON.parse(uni.getStorageSync('userInfo')),
			gridCol: 5,
			gridBorder: false,
			menuList: [
				{
					title: '基础',
					children: [
						{
							cuIcon: 'cardboardfill',
							color: 'red',
							name: '人员',
							path: 'user'
						},
						{
							cuIcon: 'recordfill',
							color: 'orange',
							name: '客户',
							path: 'customer'
						},
						{
							cuIcon: 'picfill',
							color: 'yellow',
							name: '供应商',
							path: 'supplier'
						},
						{
							cuIcon: 'noticefill',
							color: 'olive',
							name: '商品',
							path: 'goods'
						},
						{
							cuIcon: 'upstagefill',
							color: 'paymentmethod',
							name: '付款方式'
						},
						{
							cuIcon: 'brandfill',
							color: 'expenseCategory',
							name: '支付类别'
						}
					]
				}
			]
		};
	},
	methods: {
		handleClickBaseBtn(path) {
			console.log(path)
			if (path) {
				uni.navigateTo({
					url: `/pages/${path}/${path}`
				});
			}
		}
	}
};
